






















































import Vue from "vue";
import {mapState} from "vuex";

export default Vue.extend({
	name: "Header",

	methods: {
		logOut() {
			this.$store.dispatch("appLocalStorage/removeUserAuth");
			this.$router.push({name: "auth"});
		},
		translateRussian() {
			this.$i18n.locale = "ru";
		},
		translateEnglish() {
			this.$i18n.locale = "en";
		}
	},
	computed: {
		...mapState("appLocalStorage", ["isUserAuth"])
	}
});
